.search-booking {
  text-align: center;
  box-sizing: border-box;
}

.search-booking__wrapper {
  padding: 35px 20px 49px 20px;

  @media (min-width: $breakpoint-tablet-width) {
    padding: 57px 100px 59px 100px;
  }

  @media (min-width: $breakpoint-desktop-width) {
    padding: 57px 100px 66px 100px;
  }
}

.search-booking__title {
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
  margin: 0;
  margin-bottom: 22px;

  @media (min-width: $breakpoint-tablet-width) {
    font-size: 30px;
    padding-bottom: 11px;
  }
}

.search-booking__br {
  display: none;

  @media (min-width: $breakpoint-tablet-width) {
    display: inline;
  }
}

.search-booking__content {
  margin: 0;
  margin-bottom: 29px;

  @media (min-width: $breakpoint-tablet-width) {
    margin-bottom: 38px;
  }

  @media (min-width: $breakpoint-desktop-width) {
    width: 500px;
    margin: 0 auto 44px auto;
  }
}
